/**=====================
    2.21 Loader CSS Start
==========================**/
.loader-wrapper {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  background-color: $white;
  z-index: 11;
  top: 0;

  /*Color Loader*/
  .spinner{
    width: 30%;
    height: 30%;
    animation: rotate 1.5s ease-in-out infinite;
    position: relative;
  }
 
 .set1{
   display: flex;
   justify-content: center;
   align-items: center;
   gap: 75px;
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
 }
 .set2{
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
   gap: 75px;
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
 }
 
 .ball{
   width: 50px;
   height: 50px;
   background: $primary-color;
   border-radius: 50%;
 }
 
 .set1  .ball:nth-child(2){
   background: $secondary-color;
   animation: scale 1.5s ease-in-out infinite forwards;
 }
 
 .set1  .ball:nth-child(1){
   background: $secondary-color;
   animation: scale 1.5s ease-in-out infinite forwards;
 }
 
 .set2 .ball:nth-child(1){
   background: $primary-color;
   animation: scale2 1.5s ease-in-out infinite forwards;
 }
 
 .set2 .ball:nth-child(2){
   background: $primary-color;
   animation: scale2 1.5s ease-in-out infinite forwards;
 }
 
 @keyframes scale2 {
   0%{
     transform: scale(1);
   }
   50%{
     transform: scale(0);
   }
   100%{
     transform: scale(1);
   }
 }
 
 
 @keyframes scale {
   0%{
     transform: scale(0);
   }
   50%{
     transform: scale(1);
   }
   100%{
     transform: scale(0);
   }
 }
 
 @keyframes rotate{
   0%{
     transform: rotate(0deg);
   }
   100%{
     transform: rotate(360deg);
   }
 }
}

/**=====================
    2.21 Loader CSS Ends
==========================**/